class MenuDrawer extends HTMLElement {
  constructor() {
    super();
    const header = document.querySelector('.header__top-action');
    this.style.top = `${header.offsetHeight}px`;

    this.toggler = document.querySelector('[data-drawer-toggler]');
    this.toggler.addEventListener('click', this.toggle.bind(this));
  }

  toggle() {
    const state = this.getAttribute('state') === 'open' ? 'open' : 'close';

    if (state === 'open') {
      this.close();
    } else {
      this.open();
    }
  }

  open() {
    this.setAttribute('state', 'open');
  }

  close() {
    this.setAttribute('state', 'close');
  }
}

customElements.define('menu-drawer', MenuDrawer);